.cardcar-cont{
    position: relative;
    width: 100vw;
    min-height: 30vw;
    margin-top: 5vh;
    overflow: hidden;
}

.nav-btn{
    position: absolute;
    width: 5vw;
    height: 5vw;
    border-radius: 50%;
    margin-top: 8vw;
    border: none;
    text-align: center;
    background-color: rgba(255, 255, 255, 0);
    margin-left: 1vw;
}.nav-btn:hover{
    transform: scale(1.5);
    transition: transform 0.5s;
}.nav-btn:active{
    transform: scale(1.2);
    transition: transform 0.2s;
}

.right{
    margin-left: 93.5vw;
}

.cards-cont{
    position: relative;
    width: 88vw;
    height: 30vw;
    overflow: scroll;
    overflow-y: hidden;
    margin-left: 4.5vw;
}

.mid-cont{
    display: flex;
    min-width: 88vw;
    width: auto;
    position: absolute;
    left: 0;
}

.cards-cont::-webkit-scrollbar-thumb{
    background-color: black;
    border-radius: 5px;
}

@media only screen and (max-width: 600px){
    .cards-cont{
        width: 80vw;
        margin-left: 6vw;
        height: 25vh;
    }
    .right{
        margin-left: 88vw;
    }
}