.navbar-header {
    float: left;
    text-align: center;
    width: 100%;
}
.navbar-brand {float:none;}
.header-cont{
    margin-bottom: 10vh;
}

.margin-l{
    margin-left: 2vw;
}
.margin-r{
    margin-right: 2vw;
}

.buttons-nav, .shopping-cart-btn{
    position: absolute;
    border: none;
}

.cart-btn{
    position: absolute;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    font-size: 4.2vh;
    margin-left: 92vw;
    top: 1.5vh;
}.cart-btn:hover{
    transform: scale(1.2);
    transition: transform 0.5s;
}
.navbar-btn{ border: 0}
.navbar-btn:hover{
    transform: scale(1.2);
    transition: transform 0.5s;
}.navbar-btn:active{
    transform: scale(1);
    transition: transform 0.2s;
    border: 0;
}

@media only screen and (max-width: 1200px){
    .cart-btn{
        margin-left: 86vw;
    }
}

@media only screen and (max-width: 900px){
    .cart-btn{
        margin-left: 80vw
    }
}

@media only screen and (max-width: 600px){
    .cart-btn{
        margin-left: 70vw;
    }
}