.cat-card-cont, .cat-card-img{
    display: inline-block;
    height: 5vw;
    width: 5vw;
    border-radius: 25%;
    background-color: rgb(255, 255, 255);
}

.cat-card-cont{
    margin-left: 1vw;
    overflow: hidden;
    border: 1px solid black;
}

@media only screen and (max-width: 600px) {
    .cat-card-cont, .cat-card-img{
        height: 15vw;
        width: 15vw;
    }
    .cat-card-cont{
        margin-left: 5vw;
    }
}