.sell-cont{
    width: 100%;
    height: auto;
    margin-top: 15vh;
}
.intro-text{
    font-size: 5vh;
    font-weight: 1000;
    margin-left: 3vw;
}

.color-blue{
    color: rgb(0, 123, 255);
}

.light-text{
    margin-left: 3vw;
    font-size: 2vh;
    color: rgb(104, 104, 104);
}

.sell-btn{
    width: 18vh;
    height: 6vh;
    border: 0;
    border-bottom-left-radius: 2.5vh;
    border-bottom-right-radius: 2.5vh;
    border-top-right-radius: 2.5vh;
    border-top-left-radius: 2.5vh;
    margin-left: 3vw;
    background-color: rgb(0, 174, 255);
    font-size: 2.2vh;
}.sell-btn:hover{
    background-color: chocolate;
}

.how-to{
    align-self: center;
    font-size: 4.5vh;
}

.sell-cards{
    width: 100%;
    text-align: center;
    margin-top: 2vw;
}

.sellcard-cont{
    width: 90vw;
    margin-left: 5vw;
    margin-bottom: 2vw;
}

.sell-img{
    max-width: 90vw;
    width: auto;
}