.card-cont{
    position: relative;
    float: left;
    height: 25vw;
    width: 20vw;
    margin-left: 2vw;
    margin-top: 1vw;
    box-shadow: 0px 10px 10px 00px rgb(223, 223, 223);
    overflow: hidden;
}
.cardimg-cont{
    height: 15vw;
    text-align: center;
    background-color: rgb(213, 213, 213);
}
.card-img{
    display: inline-block;
    max-width: 8vw;
    width: auto;
    margin-top: 1vw;
}

.card-link{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
}

@media only screen and (max-width: 600px){
    .card-cont{
        width: 25vw;
        height: 35vw;
    }
}